.webTab {
    flex-shrink: 0;
    height: 100%;
    padding: 5px 19px 5px 5px;
    box-sizing: border-box;
    position: relative;
    background: #F8F8FF;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: default;
    border-left: 1px solid #c0c0c0;
    border-top: 1px solid transparent;
}

.webTab.active {
    background-color: #D9E3F3;
    border-top: 1px solid #c0c0c0;
}

.webTab > i {
    display: none;
    position: absolute;
    right: 3px;
    top: 0;
    bottom: 0;
    margin: auto 0;
    height: 14px;
    width: 14px;
    cursor: pointer;
    background-image: url('data:image/gif;base64,');
}

.webTab.active > i, .webTab:hover > i{
    display: block;
}
.webTab.active > i.hide, .webTab:hover > i.hide {
  display: none;
}

.webTab > i:hover {
    background-image: url('data:image/gif;base64,');
}

.webTab img {
  margin-right: 5px;
}