/*react-grid needs border-box to function properly*/
.react-grid-Container *{
  box-sizing: border-box;
}

/* overwrite default cell focus*/
.react-grid-Container .react-grid-Cell:focus {
  outline: none;
  outline-offset: 0;
}

/* Height issues for overflown container */
.react-grid-Container,
.react-grid-Container .react-grid-Main,
.react-grid-Container .react-grid-Grid {
  height: 100%
}

.react-grid-Container .react-grid-Grid {
  min-height: auto!important; /*important needed to overwrite the style inserted by js*/
}

.react-grid-Container .react-grid-Canvas { 
  height: 100%!important; /*important needed to overwrite the style inserted by js*/
}

.react-grid-Container .react-grid-Main,
.react-grid-Container .react-grid-Canvas,
.react-grid-Container .react-grid-Grid {
  background-color: transparent;
}


/* CbaCell */

/* Default overwrites*/
.react-grid-Container .react-grid-Row .react-grid-Cell {
  padding-left: 0;
  padding-right: 0;
  position: absolute;
  height: inherit;
  background: transparent;
}

.react-grid-Container .react-grid-Row .react-grid-Cell:first-child {
  padding-left: 0;
}

.react-grid-Container .CbaTreeCellInner, 
.react-grid-Container .CbaTreeCellInner-rowStart, 
.react-grid-Container .CbaTreeCellInner-expander 
.react-grid-Container .CbaTreeCellInner-image {
  height: 100%;
}

.react-grid-Container .CbaTreeCellInner {
  display: flex;
  padding-left: 3px;
  padding-right: 3px;
}

.react-grid-Container .react-grid-Cell:first-child .CbaTreeCellInner {
  padding-left: 0;
}

.react-grid-Container .CbaTreeCellInner-rowStart {
  display: flex;
}

.react-grid-Container .CbaTreeCellInner-expander {
  width: 16px;
  background-position: center;
  background-repeat: no-repeat;
}

.react-grid-Container .CbaTreeCellInner-expander.hasSubRows{
  background-image: url('data:image/png;base64,');

}

.react-grid-Container .CbaTreeCellInner-expander.hasSubRows.expanded{
  background-image: url('data:image/png;base64,');
}

.react-grid-Container .CbaTreeCellInner-image {
  width: 21px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}


.react-grid-Container .CbaTreeCellInner .CbaTreeCellInner-value {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  align-self: center;
  padding-left: 2px;
  width: 100%;
}

.react-grid-Container .CbaTreeCellInner input {
  background: transparent;
  border: 0;
  outline: 0;
  padding-left: 2px;
  width: 100%;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: inherit;
  text-align: inherit;
  color: inherit;
}



/* CbaRow*/
.react-grid-Container .react-grid-Row.react-grid-Row--odd {
  background-color: rgb(240, 240, 240);
}

.react-grid-Container .react-grid-Row.react-grid-Row--even {
  background: #fff;
  /* background: "transparent"; If rows should see the tree background*/
}

.react-grid-Container .react-grid-Row:hover .react-grid-Cell {
  background: transparent;
}

.react-grid-Container .react-grid-Row:hover {
  background: linear-gradient(rgb(235, 235, 235), rgb(213, 213, 213));
}

.react-grid-Container .react-grid-Row.selected {
  background: linear-gradient(rgb(88, 130, 181), rgb(65, 102, 147));
}

.react-grid-Container .react-grid-Row.selected .react-grid-Cell .CbaTreeCellInner, 
.react-grid-Container .react-grid-Row.selected .CbaTreeCellInner input {
  color: #fff !important; /*important needed to overwrite the style inserted by js*/
}

.react-grid-Container .react-grid-Row.cut {
  opacity: 0.7;
}


/* CbaColumns overwrites*/
.react-grid-Container .react-grid-HeaderCell {
  background: linear-gradient(rgb(249, 249, 249), rgb(228, 228, 228));
  font-family: Verdana, "Lucida Sans", Arial, Helvetica, sans-serif;
  color: #000;
  font-size: 14px;
  font-weight: normal;
  font-style: normal;
  padding: 0;
}

.react-grid-Container .react-grid-HeaderCell:hover {
  background: linear-gradient(rgb(228, 228, 228), rgb(249, 249, 249));
}

.react-grid-Container .CbaTreeHeaderInner {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  align-self: center;
  width: 100%;
  padding: 8px 3px 8px 5px;
}

.react-grid-Container .react-grid-HeaderCell-sortable--ascending .CbaTreeHeaderInner,
.react-grid-Container .react-grid-HeaderCell-sortable--descending .CbaTreeHeaderInner {
  padding-right: 20px;
}

.react-grid-Container .react-grid-HeaderCell-sortable {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.react-grid-Container .react-grid-HeaderCell-sortable > span {
  font-size: 0;
  position: absolute;
  right: 3px;
}

.react-grid-Container .react-grid-HeaderCell-sortable.react-grid-HeaderCell-sortable--ascending > span {
  width: 16px;
  height: 100%;
  background-image: url('data:image/png;base64,');
  background-position: center;
  background-repeat: no-repeat;
  transform: rotate(270deg);
}

.react-grid-Container .react-grid-HeaderCell-sortable.react-grid-HeaderCell-sortable--descending > span {
  width: 16px;
  height: 100%;
  background-image: url('data:image/png;base64,');
  background-position: center;
  background-repeat: no-repeat;
  transform: rotate(90deg);
}


/* Hide Columns */
.hideColumns .react-grid-Container .react-grid-Header {
  display: none;
}

.hideColumns .react-grid-Container .react-grid-Viewport {
  top: 0!important; /*important needed to overwrite the style inserted by js*/
}
