.webtoolbar-wrap {
    height: 25px;
    margin: 5px;
    margin-bottom: 0;
}

.webtoolbar-wrap .webtoolbar-inner {
    background-color: #D9E3F3;
    height: 100%;
}

.webtoolbar-wrap .webtoolbar-tab-container {
    background: #F8F8FF;
    height: calc(100% - 2px);
    display: flex;
}

.webtoolbar-wrap .webtoolbar-tab-container .webtoolbar-show-more {
    position: relative;
    flex: 1 0 auto;
    width: 40px;
    height: calc(100% - 1px);
    border: 1px solid #C0C0C0;
    border-bottom: 0;
}

.webtoolbar-wrap .webtoolbar-tab-container .webtoolbar-show-more > i {
    background-image: url('data:image/png;base64,');
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    width: 20px;
    height: calc(100% - 6px);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 5px;
    margin: auto;
    display: none;
    border: 1px solid transparent;
    transition: border 0.2s ease-in;
}

.webtoolbar-wrap .webtoolbar-tab-container .webtoolbar-show-more > i.show {
    display: block;
}

.webtoolbar-wrap .webtoolbar-tab-container .webtoolbar-show-more > i:hover {
    background-image: url('data:image/png;base64,');
    border: 1px solid #C0C0C0;
}

