  white-space: nowrap;
  overflow: hidden;
}

.CbaButton-highlight-up {
  -webkit-animation: CbaButton-highlight-on 1 1s linear;
          animation: CbaButton-highlight-on 1 1s linear;
  background: bisque;
}

.CbaButton-highlight-down {
  -webkit-animation: CbaButton-highlight-off 1 1s linear;
          animation: CbaButton-highlight-off 1 1s linear;
  background: azure;
}

@-webkit-keyframes CbaButton-highlight-on {
  from { background: azure; }
  to { background: bisque; }
}

@keyframes CbaButton-highlight-on {
  from { background: azure; }
  to { background: bisque; }
}

@-webkit-keyframes CbaButton-highlight-off {
  from { background: bisque; }
  to { background: azure; }
}

@keyframes CbaButton-highlight-off {
  from { background: bisque; }
  to { background: azure; }
}

/*react-grid needs border-box to function properly*/
.react-grid-Container *{
